body {
  background-image: url('wall.jpg');
}

.login-container {
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
}

.form1 {
  position: relative;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  gap: 20px;
  width: 260px;
  padding: 20px;
  background: linear-gradient(to right, #ffecd2, #fcb69f);
  font-size: 16px;
  font-weight: 500;
  padding: 20px;
  border-radius: 5px;
  border-color: #000000;
  border: 2px;
  box-shadow: 1px 2px 3px 4px rgba(0, 0, 0, 0.5);
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}

.username1 {
  display: flex;
  flex-direction: column;
  position: relative;
}


#username {
  position: relative;
  background: transparent;
  border-radius: 2px;
  border: 1px solid #000000;
  width: 250px;
  height: 36px;
  padding-left: 5px;
  z-index: 0;
}

#u1 {
  position: absolute;
  left: 5px;
  top: 9px;
  z-index: 1;
  padding-left: 2px;
  padding-right: 2px;
}

.email {
  display: flex;
  flex-direction: column;
  position: relative;
}

#email1 {
  position: relative;
  background: transparent;
  border-radius: 2px;
  border: 1px solid #000000;
  width: 250px;
  height: 36px;
  padding-left: 5px;
  z-index: 0;
}

#e1 {
  position: absolute;
  left: 5px;
  top: 9px;
  z-index: 1;
  padding-left: 2px;
  padding-right: 2px;
}

.password-input {
  display: flex;
  flex-direction: column;
  position: relative;
}





#password {
  background: transparent;
  border-radius: 2px;
  border: 1px solid #000000;
  width: 250px;
  height: 36px;
  padding-left: 5px;
  position: relative;
  z-index: 0;
}





#p1 {
  position: absolute;
  left: 5px;
  top: 9px;
  z-index: 1;
  padding-left: 2px;
  padding-right: 2px;
}

#container {
  position: relative;
}

#buttn {
  position: absolute;
  left: 208px;
  bottom: 9.2px;
  border: none;
  background-color:  #fcb69f;
  cursor: pointer;
  border-radius: 2px;
}

#buttn:hover {
  background-color: #fcb69f;
}

#loginbutton {
  border: none;
  border-radius: 2px;
  background-color:  #ffecd2;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

#loginbutton:hover {
  background-color: #fcb69f;
}


.alert1 {
  position: absolute;
  font-size: 10px;
  color: red;
  top: 270px;
  justify-self: flex-start;
}

.alert2 {
  position: absolute;
  font-size: 10px;
  color: red;
  top: 330px;
}

.alert3 {
  position: absolute;
  font-size: 10px;
  color: red;
  top: 390px;
}

#icon {
  width: 200px;
  border-radius: 75%;
}